import React from 'react';

export default function Rating({ value, text }) {
  return (
    <span className="rating">
      <i
        className={
          value >= 1
            ? 'fas fa-star'
            : value >= 0.5
            ? 'fas fa-star-half-alt'
            : 'far fa-star'
        }
      ></i>
      <i
        className={
          value >= 2
            ? 'fas fa-star'
            : value >= 1.5
            ? 'fas fa-star-half-alt'
            : 'far fa-star'
        }
      ></i>
      <i
        className={
          value >= 3
            ? 'fas fa-star'
            : value >= 2.5
            ? 'fas fa-star-half-alt'
            : 'far fa-star'
        }
      ></i>
      <i
        className={
          value >= 4
            ? 'fas fa-star'
            : value >= 3.5
            ? 'fas fa-star-half-alt'
            : 'far fa-star'
        }
      ></i>
      <i
        className={
          value >= 5
            ? 'fas fa-star'
            : value >= 4.5
            ? 'fas fa-star-half-alt'
            : 'far fa-star'
        }
      ></i>
      <span className="mx-2">{text}</span>
    </span>
  );
}
